import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Main from '../views/Main.vue'
import Note from '../views/Note.vue'
import Memory from '../views/Memory.vue'
import Works from '../views/Works.vue'
import Blog from '../views/Blog.vue'
import About from '../views/About.vue'

import NoteDetail from '../views/NoteDetail.vue'
import MemoryDetai from '../views/MemoryDetai.vue'
import WorksDetail from '../views/WorksDetail.vue'

Vue.use(VueRouter)

  const routes = [
    // { path: '/',redirect: Home,  name: 'Home',component: Home,meta: { title:'首页' }},
  {
    path: '/',
    name: 'Main',
    component: Main,
    children:[
      { path: '/', name: 'Home',component: Home,meta: { title:'首页' }},
      { path: '/Home', name: 'Home',component: Home,meta: { title:'首页' }},
      { path: '/notes', name: 'Note',component: Note,meta: { title:'心情随笔' }},
      { path: '/memories', name: 'Memory',component: Memory,meta: { title:'韶华追忆' }},
      { path: '/works', name: 'Works',component: Works,meta: { title:'个人作品' }},
      { path: '/blog', name: 'Blog',component: Blog,meta: { title:'Blog留言' }},
      { path: '/about', name: 'About',component: About,meta: { title:'关于我' }},

      { path: '/notes/:id', name: 'NoteDetail', component: NoteDetail,props: true,meta: { title:'心情随笔详情' }},
      { path: '/memories/:id', name: 'MemoryDetai',component: MemoryDetai,props: true,meta: { title:'韶华追忆详情' }},
      { path: '/works/:id', name: 'WorksDetail',component: WorksDetail,props: true,meta: { title:'个人作品详情' }},


    ]
  },
  
]

const router = new VueRouter({
  routes
})


export default router
