<template>
  <div class="warp">
    <div class="main_l">
      <!--最新作品-->
      <div class="news_works">
        <!-- 导入公共模块Left -->
        <LHerder msg="心情随笔" />
        <div class="note_detail">
          <div class="note_detail-con" v-html="model.body"> </div>
        </div>
      </div>
    </div>
    <!-- 导入公共模块Left -->
    <Left />
  </div>
</template>

<script>
import Left from "../components/Left.vue";
import LHerder from "../components/LHerder.vue";

export default {
  components: {
    Left,
    LHerder,
  },
  data() {
    return {
      model: {
        body: "",
      },
    };
  },
  created() {
    this.fetch()
    console.log('id:'+  this.$route.params.id  )
  },
  methods: {
    async fetch() {
      const res = await this.$http.get(`rest/notes/${this.$route.params.id}`);
      if (res.status == 200) {
        this.model = res.data;
      }
    },
  },
};
</script>
