<template>
  <div class="warp">
    <div class="main_l">
      <!--韶华追忆-->
      <div class="note">
       <!-- 导入公共模块Left -->
        <LHerder msg="Blog留言"/>
        <div class="blog">
          <form @submit.prevent='save'>
            <p>
              <label for="username">姓名</label>
              <input id="username" type="text" v-model="model.username" placeholder="请输入您的姓名" />
            </p>
            <p>
              <label for="tel">电话</label>
              <input id="tel" type="text" v-model="model.mobile" placeholder="请输入您的电话（选填）" />
            </p>
            <p>
              <label for="email">邮箱</label>
              <input id="email" type="email" v-model="model.email" placeholder="请输入您的邮箱（选填）" />
            </p>
            <textarea placeholder="留言内容" v-model="model.body" id="con"></textarea>
            <input type="submit" class="submit" />
          </form>
        </div>
      </div>
    </div>
    <!-- 导入公共模块Left -->
    <Left />
  </div>
</template>


<script>
import Left from "../components/Left.vue";
import LHerder from "../components/LHerder.vue";

import $ from "jquery";
import layer from "layui-layer";

export default {
    data () {
        return {
            model:{
                username:'',
                mobile:'',
                email:'',
                con:''
            }
        }
    },
  components: {
    Left,
    LHerder
  },
  methods: {
      async save(){
          if(this.model.username==''){
              layer.msg($("#username").attr("placeholder"))
              return false;
          }
           if(this.model.con==''){
              layer.msg($("#con").attr("placeholder"))
              return false;
          }
         const  res = await this.$http.post("rest/blog", this.model);
          if(res.status==0){
            layer.msg('提交成功')
          }
          console.log(this.model)
      }
  }

};
</script>