<template>
  <div class="warp">
    <div class="main_l">
      <!--韶华追忆-->
      <div class="note">
        <!-- 导入公共模块Left -->
        <LHerder msg="心情随笔"/>
        <ul class="note_ul">
          <li v-for="(item,index) in list" :key="index" @click="$router.push(`/notes/${item._id}`)">
            <div class="note_txt">
              <p class="note_txt_time">
                <span class="time"> {{item.updated}}</span>
              </p>
              <p class="note_txt_title">
                {{item.title}}
              </p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <!-- 导入公共模块Left -->
    <Left />
  </div>
</template>

<script>
import Left from "../components/Left.vue";
import LHerder from "../components/LHerder.vue";

export default {
  components: {
    Left,
    LHerder
  },
  data () {
    return {
      list:[],
    }
  },
  created () {
    this.fetch()
    
  },
  methods: {
    async fetch(){
       const res = await this.$http.get('rest/notes')
       this.list=res.data;
      console.log(res.data)
    },
  }
};
</script>

